import { Component, OnInit, ViewChild, ContentChildren, ElementRef } from '@angular/core';
import { BrowserQRCodeReader } from '@zxing/library';


declare var Quagga: any;

const codeReader = new BrowserQRCodeReader();



@Component({
  selector: 'app-scanner',
  templateUrl: './scanner.component.html',
  styleUrls: ['./scanner.component.scss']
})
export class ScannerComponent implements OnInit {

  title: string = "Scanner";
  
  scanner: any;
  state: any;
  confirmation: string = "";

  // ignore
  catId: string = "";
  name: string = "";
  foo: string = "";

  result: any = "";
  resultInfo: string = "";

  @ViewChild('wrapper', { read: ElementRef }) wrapper: ElementRef;
  @ViewChild('img', { read: ElementRef }) img: ElementRef;
  @ViewChild('label', { read: ElementRef }) label: ElementRef;
  //@ContentChildren('label', { descendants: true }) label: ElementRef; 
  

  constructor() { }

  ngOnInit(): void {
    this.state = {};
    this.scanner = Quagga;

  }

  ngAfterViewInit(): void {
    let images = this.wrapper.nativeElement.querySelector('img','.post-body');
    let img = this.img.nativeElement;
    console.log(`img.src: ${img.src}`);
    console.log(this.wrapper.nativeElement);
    console.log(this.label.nativeElement.textContent);
    console.log(images);
  }
  
  decodeFromCamera(cameraID: number): void {
    try {
      (async () => {
        cameraID = cameraID || 0;
        const videoInputDevices = await codeReader.listVideoInputDevices();
        let choosenDevice;

        videoInputDevices
        .forEach(device => console.log(`${device.label}, ${device.deviceId}`));
 


        if (videoInputDevices.length == 1) {
          choosenDevice = videoInputDevices[0];
        } else {
          choosenDevice = videoInputDevices[cameraID] || videoInputDevices[0];
        }
        
        const firstDevice = choosenDevice;
        const firstDeviceId = firstDevice.deviceId;
        console.log(`firstDeviceId: ${firstDevice.label}, ${firstDevice.deviceId}`);



        codeReader
        .decodeOnceFromVideoDevice(firstDeviceId, 'video')
        .then(result => console.log(result))
        .catch(err => console.error(err));

        //this.result = await codeReader.decodeOnceFromVideoDevice(firstDeviceId, 'video') 
        //console.log(`this.result.text:: ${this.result.text}`);
      })()
    } catch(err) {
      console.log("decodeFromCamera:::Code Reader Error!!");
      console.log(err);
    }
  }

  reset(): void {
    console.log("Code Reader has been Reset!!");
    codeReader.reset();
  }


  decodeFromImage(): void {
    try {
      (async () => {
        const img = this.img.nativeElement;
        this.result = await codeReader.decodeFromImage(img);
        console.log(`this.result.text:: ${this.result.text}`);
      })()
    } catch(err) {
      console.log("decodeFromImage:::Code Reader Error!!");
      console.log(err);
    }

  }
  
  decodeFromURL(url): void {
    try {
      (async () => {
        this.result = await codeReader.decodeFromImage(undefined, url);
        console.log(`this.result.text:: ${this.result.text}`);
        this.resultInfo = this.result.text;
      })()
    } catch(err) {
      this.resultInfo = err.toString();
      console.log("decodeFromURL:::Code Reader Error!!");
      console.log(err);
    }
  }




}
